<template>
	<view class="page">

		<view class="scroll" v-for="(item,index) in emojylist">
			<image :src="item" class="icon"></image>
			<!-- <scroll-view v-for="(item,index) in emojylist" class="scroll">
			
		</scroll-view> -->
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				emojylist: []
			}
		},
		onLoad() {
			var imagename = '';
			var path = '../../static/mipmap-hdpi/';
			for (let i = 1; i < 104; i++) {
				if (i < 10) {
					imagename = 'face_00' + i;
				} else {
					if (i < 100) {
						imagename = 'face_0' + i;
					} else {
						imagename = 'face_' + i;
					}
				}
				var item = path + imagename + '.png';
				this.emojylist.push(item);
			}
		}
	}
</script>

<style>
	.scroll {
		width: 100%;
		height: 200rpx;
		background-color: #007AFF;
	}

	.page {
		width: 100%;
		height: 400rpx;
		background-color: #1FDCA9;
		position: absolute;
		bottom: 0rpx;
	}

	.icon {
		width: 80rpx;
		height: 80rpx;
	}
</style>
